.slide-button-wrap {
  display: flex;
  flex-direction: column;
  width: fit-content;
  margin: 0.5em auto;
  justify-items: center;
  max-width: 100%;
  flex-wrap: wrap;
}

.slide-button-inner-wrap {
  display: flex;
  flex-wrap: wrap;
  width: fit-content;
  max-width: 100%;
  overflow: hidden;
  flex-wrap: wrap;
  justify-content: start;
  flex-grow: 1;
  align-items: center;
}

.slide-button-inner-wrap:first-child {
}
.slide-button-inner-wrap:last-child {
}
