.logo-wrap {
  max-height: 100%;
  position: relative;
  overflow: hidden;
  height: max-content;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.logo-wrap svg {
  position: relative !important;
  height: 100% !important;
  align-items: center;
  width: 100% !important;
  top: auto !important;
  left: auto !important;
  max-height: 100% !important;
  max-width: max-content;
}

.logo-wrap path {
  fill: ivory !important;
  fill: var(--iq-color-background-warm) !important;
}

.list-item-clear-list {
  margin: auto;
}

.list-item-clear-list button:hover {
  background-color: #942828;
  color: #fff;
  opacity: 1;
  transform: none;
}

.list-item-clear-list button {
  opacity: 0.5;
  text-transform: uppercase;
}
